@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@200&display=swap');

*{
    margin: 0;
    padding: 0;
}

.sidebar{
    height: 150vh;
    font-family: 'Oswald', sans-serif;
    width: 12rem;
    padding: 1rem -2rem;
    border: 0px solid;
    background: #722929;
    background: linear-gradient(-180deg, rgba(255, 255, 255, 1) 0%, rgba(58, 120, 193, 1) 100%);

    .navBar{
        color:rgb(0, 0, 0);
        font-weight: 800;
        font-size:40px;
        padding: 27px;
    }

    ul{
        list-style: none;
        padding: 2rem -2rem;
        li{
            a{  
                text-align:center;
                display:block;
                width: 190px;
                padding: 10px 0px;
                border: 0px solid #72a1d83d;
                background: #5285c050;
                border-radius: 1%;
                color:white;
                text-decoration: none;
                transition: 1s;
            }a:hover{
                background: #4c59689c;
            }
        }
    }
}
