@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200&display=swap);
.flex{display:flex;height:100%}body{transition:1s}.content{height:calc(100vh);overflow-y:visible}

*{margin:0;padding:0}.sidebar{height:150vh;font-family:'Oswald', sans-serif;width:12rem;padding:1rem -2rem;border:0px solid;background:#722929;background:linear-gradient(-180deg, #fff 0%, #3a78c1 100%)}.sidebar .navBar{color:#000;font-weight:800;font-size:40px;padding:27px}.sidebar ul{list-style:none;padding:2rem -2rem}.sidebar ul li a{text-align:center;display:block;width:190px;padding:10px 0px;border:0px solid #72a1d83d;background:#5285c050;border-radius:1%;color:white;text-decoration:none;transition:1s}.sidebar ul li a:hover{background:#4c59689c}

.formStyles{margin:20px auto;width:60%}.formStyles Select{margin-bottom:20px !important}.formStyles button{margin:15px}


.box{margin:15px 15px}.box ul{list-style-type:none}.grid{min-height:150%;display:flex;flex-wrap:wrap;flex-direction:row}.flexOptions{display:flex}.flexOptions .btnform{margin:10px}

ul{padding:2px;list-style-type:none}

.overviewCars{font-family:'Oswald', sans-serif;padding:36px}.overviewCars h1{margin-top:10px;padding:1px}

.NavBar{background-color:#fff;padding:1rem 2rem;border-bottom:1px solid #c3ccde}

